.webdesigntuts-workshop {
	// background: #151515;
	// height: 100%;
	// position: absolute;
	// text-align: center;
	// width: 100%;
    height: 70px;
}

.webdesigntuts-workshop:before,
.webdesigntuts-workshop:after {
	content: '';
	display: block;	
	height: 1px;
	left: 50%;
	margin: 0 0 0 -400px;
	position: absolute;
	width: 800px;
}

.webdesigntuts-workshop:before {
	background: #444;
	background: linear-gradient(left, #151515, #444, #151515);
	top: -15px;
}

.webdesigntuts-workshop:after {
	background: #444;
	background: linear-gradient(left, #151515, #000, #151515);	
	top: 60px;
}

